import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery';
@Component({
  selector: 'app-business-house-info-add-edit',
  templateUrl: './business-house-info-add-edit.component.html',
  styleUrls: ['./business-house-info-add-edit.component.scss']
})
export class BusinessHouseInfoAddEditComponent implements OnInit {


  businessLogo: File = null;
  businessBanner: File = null;
  businessFavicon: File = null;

  constructor() { }

  ngOnInit(): void {

  }
  
  //For Business Logo Upload
  onBusinessLogoSelected(event) {
    if (event.target.files.length > 0) {
      this.businessLogo = <File>event.target.files[0];
      var displayFileName = event.target.files[0].name;
      $(".business-logo-label").text(displayFileName);
    } else {
      alert("upload image");
    }
  }
  //For Business Banner Upload
  onBusinessBannerSelected(event) {
    if (event.target.files.length > 0) {
      this.businessBanner = <File>event.target.files[0];
      var displayFileName = event.target.files[0].name;
      $(".business-banner-label").text(displayFileName);
    } else {
      alert("upload image");
    }
  }
  //For Business Favicon Upload
  onBusinessFaviconSelected(event) {
    if (event.target.files.length > 0) {
      this.businessFavicon = <File>event.target.files[0];
      var displayFileName = event.target.files[0].name;
      $(".business-favicon-label").text(displayFileName);
    } else {
      alert("upload image");
    }
  }
}
